:root{--background-color: hsl(185, 41%, 84%);--main-card-color: hsl(189, 41%, 97%);--primary-color: hsl(183, 100%, 15%);--primary-color-alt: hsl(172, 67%, 45%);--secondary-color: hsl(184, 14%, 56%);--teartiary-color: hsl(186, 14%, 43%);--white: hsl(0, 0%, 100%);--red: hsl(0, 94%, 42%);--body-font: 'Space Mono', sans-serif;--normal-font-size: 1.5rem;--small--font-size: 1rem;--smaller-font-size: 0.75rem;--font-normal: 400;--font-bold: 700;--mrg-0-25: .25rem;--mrg-0-5: .5rem;--mrg-0-75: .75rem;--mrg-1: 1rem;--mrg-1-5: 1.5rem;--mrg-2: 2rem;--mrg-2-5: 2.5rem;--mrg-3: 3rem}@media screen and (min-width: 968px){:root{--big-font-size: 3rem;--h1-font-size: 2.25rem;--h2-font-size: 1.5rem;--h3-font-size: 1.25rem;--normal-font-size: 1rem;--small-font-size: .875rem;--smaller-font-size: .813rem}}*{box-sizing:border-box;padding:0;margin:0;font-family:'Space Mono', sans-serif;font-family:var(--body-font)}html{scroll-behavior:smooth}body{font-size:1.5rem;font-size:var(--normal-font-size);background-color:hsl(185, 41%, 84%);background-color:var(--background-color)}h1,h2{font-weight:700;font-weight:var(--font-bold)}h3,h4{font-weight:var(--font-semi-bold)}a{text-decoration:none}label{font-size:1rem;font-size:var(--small--font-size);color:hsl(183, 100%, 15%);color:var(--primary-color)}.container{display:flex;flex-flow:column nowrap;justify-content:space-evenly;align-items:center;min-height:100vh}img{justify-self:center}@media (min-width: 359.9px) and (max-width: 1439.9px){.logo{margin:2rem}}

input::-webkit-input-placeholder{color:var(--secondary-color)}input:-ms-input-placeholder{color:var(--secondary-color)}input::placeholder{color:var(--secondary-color)}input[type="number"]::-webkit-outer-spin-button,input[type="number"]::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type="number"]{-moz-appearance:textfield}input[type="number"]:hover,input[type="number"]:focus{-moz-appearance:number-input}input[type=number]::-webkit-inner-spin-button,input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}.bill .form-input,.people .form-input{margin:.25rem 0 1rem;position:relative}.bill .validation-label,.people .validation-label{display:none}.bill .error-label,.people .error-label{color:var(--red);text-align:right;padding-left:1.5rem}.bill .error-input,.people .error-input{border:1px solid var(--red) !important}.bill input,.people input{width:100%;margin-top:var(--mrg-0-5);padding:.5rem 2.5rem .5rem 2.5rem;background-color:var(--main-card-color);outline:0;border:1px solid transparent;border-radius:.5rem;font-size:var(--h3-font-size);font-weight:var(--font-bold);color:var(--primary-color);text-align:right}.bill .input-icon,.people .input-icon{position:absolute;bottom:1rem;margin-left:var(--mrg-1);display:flex;justify-content:center;align-content:center}.bill input[type="number"]:focus,.people input[type="number"]:focus{border:1px solid var(--primary-color-alt)}.percentage .buttons{display:grid;grid-template-columns:repeat(3, 32%);grid-gap:.4rem;gap:.4rem;margin:.25rem 0 1rem;position:relative}.percentage input[type="checkbox"]{display:none}.percentage input[type="checkbox"]+label{background-color:var(--primary-color);outline:0;border:0px;border-radius:.5rem;padding:.5rem 1rem;color:var(--white);font-size:var(--h3-font-size);font-weight:var(--font-bold);transition:.25s;text-align:center;cursor:pointer}.percentage input[type="checkbox"]:checked+label{background-color:var(--primary-color-alt);color:var(--primary-color);transition:.25s}.percentage input[type="checkbox"]:hover+label{background-color:var(--background-color);color:var(--primary-color);transition:.25s}.percentage input[type="number"]{padding:.5rem 1rem;background-color:var(--main-card-color);outline:0;border:1px solid transparent;border-radius:.5rem;font-size:var(--h3-font-size);font-weight:var(--font-bold);color:var(--primary-color)}.percentage input[type="number"]:focus{border:1px solid var(--primary-color-alt)}@media (min-width: 359.9px) and (max-width: 1439.9px){.bill{padding:.5rem}.bill label{margin-left:3rem}.bill .form-input,.people .form-input{margin-left:2.5rem}.bill input,.people input{width:90%}.percentage{margin:0;padding:0}.percentage .buttons{display:grid;grid-template-columns:repeat(2, 40%);position:relative;justify-content:center}.percentage label{margin-left:3rem}.percentage .buttons label{margin-left:0}.percentage input[type="checkbox"]+label{padding:.5rem .5rem}.people label{margin-left:3rem}}

.card-total{background-color:var(--primary-color);border-radius:.5rem;padding:2rem 1.5rem;display:flex;flex-flow:column nowrap;justify-content:space-between}.tip-amount{display:flex;align-content:center;justify-content:space-between}.tip-amount .tip h3{font-size:var(--small--font-size);color:var(--white)}.tip-amount .tip span{font-size:var(--smaller-font-size);color:var(--teartiary-color);font-weight:var(--font-bold)}.tip-amount h2{font-size:var(--h1-font-size);color:var(--primary-color-alt)}.reset{background-color:var(--primary-color-alt);border-radius:.5rem;font-size:var(--small--font-size);font-weight:var(--font-bold);outline:0;border:1px solid transparent;padding:.5rem 1rem;width:100%}.reset:disabled{-webkit-filter:opacity(70%);filter:opacity(70%)}@media (min-width: 359.9px) and (max-width: 1439.9px){.card-total{margin:0 2rem 1rem}}

